.app-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
}

table {  
  border-collapse: collapse;
  width: 80%;
}

th,
td {
  border: 1px solid #b98888;
  text-align: left;
  padding: 8px;
  font-size: 22px;
}

th {
  background-color: rgb(152, 14, 120);
}

td {
  background-color: rgb(11, 73, 112);
}

form {
  display: flex;
  gap: 5px;
}

form td:last-child {
  display: flex;
  justify-content: space-evenly;
}

form * {
  font-size: 18px;
}
h2{
    color: teal;
}